export function chart_Data(szMidarea) {
  var baArea = [];
  var nsArea = [];
  var ftArea = [];
  var lhuArea = [];
  var lgArea = [];
  var ytArea = [];
  var lhArea = [];
  var psArea = [];
  var gmArea = [];

  var labels = [
    "宝安区",
    "南山区",
    "福田区",
    "罗湖区",
    "龙岗区",
    "盐田区",
    "龙华区",
    "坪山区",
    "光明区",
  ];
  baArea = szMidarea.filter((area) => area.includes("宝安"));
  nsArea = szMidarea.filter((area) => area.includes("南山"));
  ftArea = szMidarea.filter((area) => area.includes("福田"));
  lhuArea = szMidarea.filter((area) => area.includes("罗湖"));
  lgArea = szMidarea.filter((area) => area.includes("龙岗"));
  lhArea = szMidarea.filter((area) => area.includes("龙华"));
  ytArea = szMidarea.filter((area) => area.includes("盐田"));
  psArea = szMidarea.filter((area) => area.includes("坪山"));
  gmArea = szMidarea.filter((area) => area.includes("光明"));
  const config = {
    data: {
      labels,
      datasets: [
        {
          label: "NCOV Risk Area",
          data: [
            baArea.length,
            nsArea.length,
            ftArea.length,
            lhuArea.length,
            lgArea.length,
            ytArea.length,
            lhArea.length,
            psArea.length,
            gmArea.length,
          ],
          backgroundColor: [
            "rgb(255, 99, 132)",
            "rgb(54, 162, 235)",
            "rgb(255, 205, 86)",
            "rgb(25, 135, 84)",
            "rgb(108,117,125)",
            "rgb(13,202,240)",
            "rgb(33,37,41)",
            "rgb(13,110,253)",
            "rgb(220,53,69)",
          ],
          hoverOffset: 4,
          borderRadius: 20,
          spacing: 10,
        },
      ],
    },
    options: {
      cutout: 125,
    },
  };

  return config;
}
